import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import '../sass/contact.scss'
function Contact({breadCrumbHandle}) {
    useEffect(() => {
        breadCrumbHandle({
            content: 'Lin h',
            linkContent:'',
            contentMain: '',
            type: 1,
        })
    }, [])
    return (
        <div className='contact'>
            <div className='contact__container container'>
                <div className='grid'>
                    <div className='row'>
                        <div className='col c-6 l-12'>
                            <div className='boxControl'>
                                <h4>NH HNG DOLA RESTAURANT</h4>
                                <div className='info'>
                                    <ul>
                                        <li>
                                            <p>Nh hng chng ti lun lun t khch hng ln hng u, tn tm phc v, mang li cho khch hng nhng tri nghim tuyt vi nht. Cc mn n vi cng thc c quyn s mang li hng v mi m cho thc khch. Dola Restaurant xin chn thnh cm n.</p>
                                        </li>
                                        <li>
                                            <h5>Ca hng chnh</h5>
                                        </li>
                                        <li>
                                            <strong>a ch:</strong><span>70 L Gia, phng 15, qun 11, TP.HCM</span> 
                                        </li>
                                        <li>
                                            <strong>in thoi:</strong><a className="footer__number" href="tel:19006750"> 1900 6750</a>
                                        </li>
                                        <li>
                                            <strong>Email:</strong><a className="footer__email" href="mailto:support@sapo.vn"> support@sapo.vn</a>
                                        </li>
                                        <li  className='gohome'>
                                            <Link to="/">V trang ch</Link>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className='col c-6 l-12'>
                            <div className='boxControl'>
                                <h4>LIN H VI CHNG TI</h4>
                                <div className='info'>
                                    <form>
                                        <input placeholder='H v tn'></input>
                                        <input placeholder='Email' type='email'></input>
                                        <input placeholder='in thoi' type='number'></input>
                                        <textarea placeholder='Ni dung' rows='3'></textarea>
                                        <button>Gi thng tin</button>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact